/* header styles*/
.header {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-flow: row wrap;
   }

  /*logo/h1 styles*/
  .logo {
    font-family: 'Arapey', serif;
    font-style: italic;
    font-size: 1.6em;
    text-transform: lowercase;
    margin-bottom: 0;
    margin-top: 30px;
    width: 100%;
    color: var(--theme-header-logo);
  }

/*logo in second font for emphasis*/
  .logo span {
    font-family: 'Montserrat', sans serif;
    font-style: normal;
    font-weight: bold;
    font-size: 1.3em;
    color: var(--theme-header-logo-span)!important;
  }

/*h2 text styles*/
  .h2 {
    font-family: 'Arapey', serif;
    font-size: 1.5em;
    margin-top: -15px;
    padding-top: 9px;
    margin-bottom: 10px;
    color: var(--theme-header-logo);

  }

/*navigation styles*/
  #navbar {
    font-family:'Montserrat', sans serif;
    font-size: 1.1rem;
    font-weight: 500;
    padding: 0;
    width: 100%;
    padding: 0;
  }

  .nav {
    margin: 0;
    padding: 0;
  }
  
  .nav li {
    list-style: none;
    padding-top: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid #F0E6EF;
  }

  /* Style the navbar links */
  .header a {
    text-decoration: none;
    text-transform: uppercase;
    color: var(--theme-header-navbar);
    font-weight: 500;
  }
  
  /* Change the background color on mouse-over */
  .header a:hover {
    text-decoration: none;
    color: var(--theme-header-navbar-hover);
    font-weight: 600;
    cursor: cell;
  }

  toggle-style {
    color: var(--theme-toggle);
  }

  .MuiFormControlLabel-root {
    margin: 0!important;
    justify-content: center!important;
  }

  @media (min-width: 875px) {

    .logo-container {
      width: 50%;
      margin-top: 40px;
    }
    
    .logo {
      font-size: 2.7rem;
    }

    .logo span {
      font-size: 2.6rem;
      color: #424B54;
    }

    .h2 {
      font-size: 2rem;
      margin-top: -20px;
    }

    #navbar {
      width: 45%;
      margin-top: 40px;
    }

    .nav {
      display: flex;
      justify-content: space-evenly;
    }

    .nav li {
      border-bottom: none;
    }
}
.body {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;

}

h2 {
    background-color: var(--theme-page-h2-subheader-bg);
    color: var(--theme-page-h2-subheader-font);
    width: 100%;
    font-family: 'Arapey', serif;
    font-style: italic;
    text-transform: lowercase;
}

#about {
    margin: 30px 10px 30px 10px;
    font-size: 1.1rem;
    line-height: 1.5em;
    color: var(--theme-body-about-text);
}

#about .img {
    width: 50%;
    max-width: 200px;
    border: 3px solid white;
    border-radius: 50%;
    padding: 5px;
    margin: 0 60px 10px 0;
    cursor: cell;
    float: left;
    shape-outside: circle(40%);
}

#about .img:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.about-content p {
    line-height: 1.7em;
    text-align: left;
    padding-left: 1rem;
}

.about-content a {
    text-decoration: none;
    font-weight: bold;
    color: var(--theme-body-about-link);
    font-size: 1em;
    font-family: 'Montserrat', sans-serif;
  }
  
  .about-content a:hover {
    color: var(--theme-body-about-link-hover);
  }

#portfolio {
    width: 100%;
}

#portfolio .portfolio-images {
    width: 100%;
    border: 1px solid #424B54;
}

#portfolio .portfolio-container {
    display: flex;
    justify-content: center;
    flex-direction: column;
}

#skills {
    margin: 30px 10px 30px 10px;
    font-size: 1.1rem;
    line-height: 1.5em;
    color: var(--theme-body-about-text);
    width: 100%;
}

.skills-content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: row wrap;
}

.skills-content-col1,
.skills-content-col2,
.skills-content-col3 {
    width: 100%;
    margin-top: 0;
    margin-bottom: 0;
}

.skills-content-col1 h5,
.skills-content-col2 h5,
.skills-content-col3 h5 {
    font-weight: bold;
    font-family: 'Montserrat', sans-serif;
    text-align: center;
    margin-top: 5px;
    margin-bottom: 0;
}

.skills-content-col2,
.skills-content-col3 {
    border-top: 1px solid #eee;
}

.skills-content ul {
	list-style: none;
	padding: 0;
}

.skills-content li {
    padding: 0;
}

#contact {
    width: 100%;
}

#contact ul {
    display: flex;
    justify-content: space-around;
    align-items: stretch;
    width: 100%;    
    margin: 0;
    padding: 0;
}
  
#contact li {
    display: block;
    list-style-type: none;
  }

.icons,
.icons a {
    font-size: 3rem;
    color:var(--theme-page-contact-icons);
    cursor: cell;
}

.icons a:hover {
    color: var(--theme-page-contact-icons-hover);
}


@media (min-width: 700px) and (max-width: 1089px) {
    #about {
        width: 70%;
        font-size: 1.2rem;
    }

    #portfolio, 
    #skills,
    #contact 
    {
        width: 70%;
    }

    .skills-content {
        justify-content: space-between;
        align-items: flex-start;
    }

    .skills-content-col1,
    .skills-content-col2,
    .skills-content-col3 {
        flex-basis: 45%;
    }

    /* .skills-content-col1 {
        border-right: 1px solid #eee;
    } */

    .skills-content-col2,
    .skills-content-col3 {
        border-top: none;
    }

    h2 {
        font-size: 1.8em;
    }
}

@media (min-width: 1090px) {
    #about {
        width: 60%;
        font-size: 1.2rem;
        line-height: 2.6rem;
    }

    #portfolio,
    #skills,
    #contact {
        width: 60%;
    }

    .skills-content {
        align-items: flex-start;
    }

    .skills-content-col1,
    .skills-content-col2,
    .skills-content-col3 {
        flex-basis: 45%;
    }

    /* .skills-content-col1 {
        border-right: 1px solid #eee;
    } */

    .skills-content-col2,
    .skills-content-col3 {
        border-top: none;
    }

    h2 {
        font-size: 1.9em;
    }
};
