:root {
  margin: 0;
}

body {
  min-height: 100%;
  margin: 0;
  margin-bottom: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  text-align: center;
  margin: 0;
  padding-bottom: 10px;
}

.swiper-container {
  width: 480px;
}

.swiper-wrapper {
  -webkit-padding-start: 0;
          padding-inline-start: 0;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column wrap;
}

@media screen and (min-width: 640px) {
  .swiper-container {
    width: 640px;
  }
}

@media screen and (min-width: 768px) {
  .swiper-container {
    width: 768px;
  }
}
.swiper-container {
  width: 100% !important;
  border: 1px solid black;
}

.swiper-wrapper {
  -webkit-padding-start: 0;
          padding-inline-start: 0;
  padding-bottom: 20px;
}

img {
    width: 85%;
    border: 1px solid #1E2D24;
    margin-top: -1px;
}

.cardTextWrapper {
  font-family: 'Poppins', sans-serif;
  text-align: center;
  padding: 0 1rem 0 1rem;
  color: var(--theme-swiper-font);
  margin: 15px;

}

.cardTextWrapper h3 {
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
}

.cardTextWrapper h3 span {
  font-family: 'Poppins', sans-serif;
  font-weight: normal;
  font-size: 1rem;
}

.cardTextWrapper p {
  font-weight: normal;
  font-size: 1rem;
  text-align: left;
  line-height: 1.5em;
}

.cardTextWrapper a {
  font-family: 'Montserrat', sans-serif;
  text-decoration: none;
  font-weight: bold;
  color: var(--theme-swiper-link);
  font-size: 1.1em;
}

.cardTextWrapper a:hover {
  background-color: var(--theme-swiper-link-hover);
  padding: 0 .5rem 0 .5rem;
}

.swiper-button-prev {
  color: var(--theme-swiper-controls);
  margin-top: -100px;
}

.swiper-button-next {
  color: var(--theme-swiper-controls);
  margin-top: -100px;

}

.swiper-pagination-bullet {
  background: var(--theme-swiper-bullets);
  opacity: 1;
  }


.swiper-pagination-bullet-active {
  background: var(--theme-swiper-selected-bullet);
  opacity: 1;
  }
  
  


@media screen and (min-width: 600px) {
  .swiper-container {
    width: 640px;
  }

  .cardTextWrapper {
    font-size: 1rem;
  }
}

@media screen and (min-width: 768px) {
  .swiper-container {
    width: 768px;
  }
}

.footer {
    font-family: 'Montserrat', sans-serif;
    font-size: .6rem;
    text-transform: uppercase;
    color: var(--theme-footer-icons);
    margin-top: 50px;
}

.footer-text {
    margin: 0;
    color: var(--theme-footer-font)
}

.footer a {
    color: var(--theme-footer-font-hover);
    text-decoration: none;
}

@media (min-width: 760px) and (max-width: 1089px) {
    .footer {
        font-size: .8em;
    }
}

@media (min-width: 1090px) {
    .footer {
        font-size: .95em;
    }
}
